export default function reducer(state, action) {
  const { type, payload } = action;

  switch (type) {
    case "updateInput":
      let newSettings = JSON.parse(localStorage.settings);
      newSettings = {
        ...newSettings,
        [payload.field]: payload.value
      };
      localStorage.settings = JSON.stringify(newSettings);
      return {
        ...state,
        [payload.field]: payload.value
      };
    case "updateBars":
      return {
        ...state,
        bars: payload,
        duration: state.intervals * (state.rest + state.work)
      };
    case "startWorkout":
      return {
        ...state,
        working: true
      };
    case "finishWorkout":
      return {
        ...state,
        working: false
      };
    case "getSettings":
      const settings = JSON.parse(localStorage.settings);
      return {
        ...state,
        intervals: settings.intervals,
        rest: settings.rest,
        work: settings.work
      };

    default:
      return state;
  }
}
